        <div class="services">
          <div class="service" v-for="task in tasks" :key="task.id">
            <div class="service-icon">
              <i :class="`fa ${task.icon}`"></i>
            </div>
            <div class="service-content">
              <h4>{{ task.title }}</h4>
              <p>
                {{ task.desc }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "What-We-Do",
  data() {
    return {
      tasks: []
    };
  },
  async created() {
    const response = await axios.get("/json/what-we-do.json");
    this.tasks = response.data;
  }
};
</script>

