import axios from "axios";

const state = {
  navbarItems: []
};

const getters = {
  navbarItems: state => state.navbarItems
};

const actions = {
  fetchNavbarItems: ({ commit }) => {
    axios
      .get("/json/navbar.json")
      .then(response => commit("setNavbarItems", response.data))
      .catch(err => console.log(err));
  }
};

const mutations = {
  setNavbarItems: (state, data) => (state.navbarItems = data)
};

export default {
  state,
  getters,
  actions,
  mutations
};
