<template>
  <section class="images-slider">
    <h2 class="title">Images Slider</h2>
    <div
      id="scrollView"
      class="scroll-view"
      @mousedown="handleMouseDown"
      @mouseup="handleMouseUp"
      @mousemove="handleMouseMove"
    >
      <div class="h-scroll">
        <i class="fa fa-chevron-left left-btn" @click="shiftLeft"></i>
        <i class="fa fa-chevron-right right-btn" @click="shiftRight"></i>
        <div
          class="item"
          v-for="(item, index) in data"
          :class="index === activeSlide ? 'active-slide' : ''"
          :key="item"
          :style="{
            minWidth: shift + '%',
            left: (-1 + index) * shift + '%'
          }"
        >
          <img :src="item" alt="image" />
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  name: "ImagesSlider",
  data() {
    return {
      data: [
        "images/about/1.png",
        "images/about/2.png",
        "images/about/3.png",
        "images/about/4.png",
        "images/about/5.png",
        "images/about/6.png",
        "images/about/7.png"
      ],
      shift: 33.33,
      shiftIndex: -1,
      hiddenItem: 0,
      scrollView: null,
      dragged: false,
      mouseX: 0,
      pageX: 0,
      activeSlide: null
    };
  },
  methods: {
    shiftLeft: function() {
      if (Math.abs(this.shiftIndex) === this.data.length - 2) {
        this.shiftIndex = 0;
      } else {
        --this.shiftIndex;
      }
      this.hiddenItem = this.data[0];
      this.data.splice(0, 1);
      this.data.push(this.hiddenItem);
    },
    shiftRight: function() {
      if (Math.abs(this.shiftIndex) === this.data.length - 2) {
        this.shiftIndex = 0;
      } else {
        ++this.shiftIndex;
      }
      this.hiddenItem = this.data[this.data.length - 1];
      this.data.splice(this.data.length - 1, 1);
      this.data.unshift(this.hiddenItem);
    },
    handleMouseDown: function(e) {
      this.scrollView.style.cursor = "grabbing";
      this.dragged = true;
      this.mouseX = e.pageX;
    },
    handleMouseUp: function() {
      this.scrollView.style.cursor = "grab";
      this.dragged = false;
      this.mouseX = 0;
    },
    handleMouseMove: function(e) {
      this.pageX = e.pageX;
      if (this.dragged === true) {
        if (this.pageX <= this.mouseX) {
          this.shiftLeft();
        } else {
          this.shiftRight();
        }
        this.dragged = false;
      }
    }
  },
  mounted() {
    const element = document.getElementById("scrollView");
    this.scrollView = element;
  }
};
</script>
<style lang="scss" scoped>
.images-slider {
  box-sizing: border-box;
  .title {
    text-align: center;
    font-size: 2rem;
    font-family: Georgia, "Times New Roman", Times, serif;
    margin: 25px;
  }
  .scroll-view {
    width: 90%;
    height: 300px;
    margin: auto;
    .h-scroll {
      display: flex;
      position: relative;
      height: 100%;
      overflow: hidden;
      cursor: grab;
      .left-btn,
      .right-btn {
        position: absolute;
        width: 50px;
        height: 50px;
        background: brown;
        color: #fff;
        cursor: pointer;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: 0.7s;
      }
      &:hover .left-btn,
      &:hover .right-btn {
        opacity: 1;
      }
      .left-btn {
        top: 50%;
        left: 0;
      }
      .right-btn {
        top: 50%;
        right: 0;
      }
      .item {
        height: 300px;
        text-align: center;
        line-height: 100%;
        font-size: 2rem;
        position: absolute;
        transition: all 0.5s;
        top: 0;
        left: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .active-slide {
        border: 1px solid darkkhaki;
        padding: 10%;
        animation: fadeIn 0.7s 1 forwards;
        @keyframes fadeIn {
          0% {
            opacity: 0;
            border-radius: none;
          }
          100% {
            opacity: 1;
            border-radius: 50%;
          }
        }
      }
    }
  }
}
</style>
