<template>
  <section>
    <ImageBanner title="team member" subTitle="heart of business loving people">
    </ImageBanner>
    <div class="members-container">
      <TeamMember
        v-for="member in team"
        :key="member.id"
        :member="member"
      ></TeamMember>
    </div>
    <Footer></Footer>
    <ScrollToTop></ScrollToTop>
  </section>
</template>

<script>
import axios from "axios";
import ImageBanner from "@/components/ImageBanner";
import TeamMember from "@/components/TeamMember";
import Footer from "@/components/Footer";
import ScrollToTop from "@/components/ScrollToTop";
export default {
  name: "Team",
  data() {
    return {
      team: []
    };
  },
  components: { TeamMember, ImageBanner, Footer, ScrollToTop },
  async created() {
    const response = await axios.get("/json/team.json");
    this.team = response.data;
  }
};
</script>

<style lang="scss">
.members-container {
  padding: 50px 25px;
  display: flex;
  flex-wrap: wrap;
}
</style>
