<template>
  <section class="services-view">
    <ImageBanner
      title="our services"
      subTitle="heart of business loving people"
    ></ImageBanner>
    <div class="services">
      <Service
        v-for="service in services"
        :key="service.id"
        :data="service"
      ></Service>
    </div>
    <div class="quotes-slider">
      <h5 class="sub-title">
        we care our client
      </h5>
      <h3 class="title">
        Our Client Say
      </h3>
      <QuotesSlider></QuotesSlider>
    </div>
    <Footer></Footer>
    <ScrollToTop></ScrollToTop>
  </section>
</template>

<script>
import axios from "axios";
import ImageBanner from "@/components/ImageBanner";
import Service from "@/components/Service";
import QuotesSlider from "@/components/QuotesSlider";
import Footer from "@/components/Footer";
import ScrollToTop from "@/components/ScrollToTop";
export default {
  name: "Services",
  data: function() {
    return {
      services: []
    };
  },
  components: { ImageBanner, Service, QuotesSlider, Footer, ScrollToTop },
  async created() {
    const response = await axios.get("/json/services.json");
    this.services = response.data;
  }
};
</script>

<style lang="scss" scoped>
.services-view {
  .services {
    display: flex;
    flex-wrap: wrap;
    padding: 50px;
  }
  .quotes-slider {
    text-align: center;
    margin-top: 50px;
    .sub-title {
      font-size: 1.5rem;
      font-weight: 200;
    }
    .title {
      font-size: 2.2rem;
      margin-top: 10px;
    }
  }
}
</style>
